/**/




.greetingForm {
    /*background-color: white;*/
    padding: 1.5rem;
    border-radius: 0.375rem;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
    max-width: 28rem;
    margin: 0 auto;
}

.text {
    margin-bottom: 1rem;
    color: #4B5563; /* Tailwind's text-gray-600 */
}

.inputAndButtonContainer {
    display: flex;
    align-items: center;
    margin-bottom: 0.5rem;
}

.inputAndButtonContainer input {
    border: 1px solid #EF4444; /* Tailwind's border-red-500 */
    border-radius: 0.375rem;
    padding: 0.5rem 1rem;
    flex-grow: 1;
}

.inputAndButtonContainer button {
    margin-left: 0.5rem;
    background-color: #0066CC; /* Tailwind's bg-blue-500 */
    color: white;
    padding: 0.5rem 1rem;
    border-radius: 5px;
    text-transform: capitalize;
}

.error {
    color: #EF4444; /* Tailwind's text-red-500 */
    margin-top: 0.25rem;
}

.greeting {
    margin-top: 1rem;
    color: #4B5563; /* Tailwind's text-gray-600 */
}

.errorInput {
    border: 1px solid #EF4444; /* Tailwind's border-red-500 */
}